// api/spreadsheetApi.ts

/**
 * API handler for interacting with AppSheet spreadsheet
 */

// Define the transaction interface
export interface Transaction {
    name: string;
    type: string; // "Income" or "Expense"
    amount: number;
    transactionType: string; // "Cash", "Card", etc.
    category: string;
    note?: string;
    date: string; // YYYY-MM-DD format
  }
  
  // Replace with your actual web app URL
  const APPSHEET_API_URL = "https://script.google.com/macros/s/AKfycbyJ6KzWs1QdL86o0gflTFKHZvGqpeYNh9cfjbRyV9A6vf2AL-Cx8lOFrFONursaOPHSiQ/exec";
  
  /**
   * Add a new transaction to the spreadsheet
   * @param transaction The transaction data to add
   * @returns Promise with the response data
   */
  export async function addTransaction(transaction: Transaction): Promise<any> {
    try {
      const response = await fetch(`${APPSHEET_API_URL}?action=addTransaction`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(transaction),
      });
  
      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
  
      return await response.json();
    } catch (error) {
      console.error("Error adding transaction:", error);
      throw error;
    }
  }
  
  /**
   * Get all transactions from the spreadsheet
   * @returns Promise with the transactions data
   */
  export async function getTransactions(): Promise<Transaction[]> {
    try {
      const response = await fetch(`${APPSHEET_API_URL}?action=getTransactions`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
  
      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
  
      return await response.json();
    } catch (error) {
      console.error("Error fetching transactions:", error);
      throw error;
    }
  }