// components/dashboard/TransactionButtons.tsx
"use client";

import { useState } from "react";
import { addTransaction } from "@/app/api/putTransaction";
import { PlusCircle, MinusCircle, X } from "lucide-react";

export default function TransactionButtons() {
  const [showModal, setShowModal] = useState(false);
  const [transactionType, setTransactionType] = useState<"Income" | "Expense">("Income");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [statusMessage, setStatusMessage] = useState({ text: "", type: "" });
  
  const [formData, setFormData] = useState({
    name: "",
    amount: "",
    transactionType: "Cash",
    category: "",
    note: "",
    date: new Date().toISOString().split("T")[0],
  });

  // Payment method options
  const paymentMethods = ["Cash", "UPI", "Bank Transfer", "Credit Card", "Debit Card"];
  
  // Category options
  const categories = {
    Income: ["Salary", "Business", "Investment", "Rental", "Gift", "Other"],
    Expense: ["Food", "Transport", "Housing", "Bills", "Shopping", "Healthcare", "Education", "Entertainment", "Other"]
  };

  // Reset form data
  const resetForm = () => {
    setFormData({
      name: "",
      amount: "",
      transactionType: "Cash",
      category: "",
      note: "",
      date: new Date().toISOString().split("T")[0],
    });
    setStatusMessage({ text: "", type: "" });
  };

  // Open modal with specific transaction type
  const openModal = (type: "Income" | "Expense") => {
    resetForm();
    setTransactionType(type);
    setShowModal(true);
  };

  // Handle form field changes
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  // Submit transaction
  const submitTransaction = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setStatusMessage({ text: "", type: "" });

    try {
      await addTransaction({
        name: formData.name,
        type: transactionType,
        amount: parseFloat(formData.amount),
        transactionType: formData.transactionType,
        category: formData.category,
        note: formData.note,
        date: formData.date,
      });

      setStatusMessage({ 
        text: `${transactionType} added successfully!`, 
        type: "success" 
      });
      
      // Clear form
      resetForm();
      
      // Close modal after a short delay
      setTimeout(() => {
        setShowModal(false);
      }, 1500);
    } catch (error) {
      setStatusMessage({ 
        text: `Failed to add ${transactionType.toLowerCase()}. Please try again.`, 
        type: "error" 
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="rounded-lg border border-gray-200 bg-white p-5 shadow-sm">
      <h3 className="mb-5 text-lg font-medium text-gray-900">Quick Actions</h3>
      
      <div className="grid grid-cols-2 gap-4">
        <button
          onClick={() => openModal("Income")}
          className="flex items-center justify-center gap-2 rounded-lg bg-emerald-50 p-4 transition hover:bg-emerald-100"
        >
          <PlusCircle className="text-emerald-600" size={20} />
          <span className="font-medium text-emerald-700">Add Income</span>
        </button>
        
        <button
          onClick={() => openModal("Expense")}
          className="flex items-center justify-center gap-2 rounded-lg bg-rose-50 p-4 transition hover:bg-rose-100"
        >
          <MinusCircle className="text-rose-600" size={20} />
          <span className="font-medium text-rose-700">Add Expense</span>
        </button>
      </div>

      {/* Unified Transaction Modal */}
      {showModal && (
        <div className="fixed inset-0 z-10 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg p-6 max-w-md w-full relative">
            <button 
              className="absolute top-4 right-4 text-gray-500 hover:text-gray-700"
              onClick={() => setShowModal(false)}
              aria-label="Close"
            >
              <X size={20} />
            </button>
            
            <h3 className={`text-xl font-bold mb-5 ${
              transactionType === "Income" ? "text-emerald-600" : "text-rose-600"
            }`}>
              Add {transactionType}
            </h3>
            
            {statusMessage.text && (
              <div className={`mb-4 p-3 rounded text-sm font-medium ${
                statusMessage.type === "success" 
                  ? "bg-emerald-50 text-emerald-700" 
                  : "bg-rose-50 text-rose-700"
              }`}>
                {statusMessage.text}
              </div>
            )}
            
            <form onSubmit={submitTransaction} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="name">
                  Description
                </label>
                <input
                  id="name"
                  name="name"
                  type="text"
                  value={formData.name}
                  onChange={handleChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="What is this transaction for?"
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="amount">
                  Amount (₹)
                </label>
                <div className="relative">
                  <span className="absolute inset-y-0 left-0 flex items-center pl-3 text-gray-500">₹</span>
                  <input
                    id="amount"
                    name="amount"
                    type="number"
                    step="1"
                    min="1"
                    value={formData.amount}
                    onChange={handleChange}
                    className="w-full pl-8 pr-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="0"
                    required
                  />
                </div>
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="date">
                    Date
                  </label>
                  <input
                    id="date"
                    name="date"
                    type="date"
                    value={formData.date}
                    onChange={handleChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="transactionType">
                    Payment Method
                  </label>
                  <select
                    id="transactionType"
                    name="transactionType"
                    value={formData.transactionType}
                    onChange={handleChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    {paymentMethods.map((method) => (
                      <option key={method} value={method}>
                        {method}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="category">
                  Category
                </label>
                <select
                  id="category"
                  name="category"
                  value={formData.category}
                  onChange={handleChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                >
                  <option value="" disabled>Select category</option>
                  {categories[transactionType].map((category) => (
                    <option key={category} value={category}>
                      {category}
                    </option>
                  ))}
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="note">
                  Note (Optional)
                </label>
                <textarea
                  id="note"
                  name="note"
                  value={formData.note}
                  onChange={handleChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  rows={2}
                  placeholder="Add additional details here"
                />
              </div>
              
              <button
                type="submit"
                disabled={isSubmitting}
                className={`w-full mt-2 py-2.5 px-4 rounded-md text-white font-medium focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 ${
                  transactionType === "Income"
                    ? "bg-emerald-600 hover:bg-emerald-700 focus:ring-emerald-500"
                    : "bg-rose-600 hover:bg-rose-700 focus:ring-rose-500"
                }`}
              >
                {isSubmitting ? "Processing..." : `Save ${transactionType}`}
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}